/* If Section.css provides .section styling, we only add or override what we need */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.future-section {
  /* We'll rely on Section.css for main layout unless we need unique styling. */
}

.future-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) MAIN CONTENT LAYOUT
===================================== */
.future-content {
  display: block;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.future-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem; /* spacing between blocks */
}

.future-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.future-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) RADAR CHART BLOCK
===================================== */
.future-radar {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.future-radar h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.future-radar p {
  margin-bottom: 1rem;
  color: #333;
}

/* The slider for techComfort */
.future-slider {
  margin-bottom: 1.5rem;
}

.future-slider label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.future-slider input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

/* =====================================
   5) CHART CONTAINER
===================================== */
.future-chart-container {
  margin-top: 1rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* Ensure the chart can adapt to container size */
.chart-wrapper {
  position: relative;
  height: 400px; /* or any desired default height */
  width: 100%;
}

/* =====================================
   6) RESPONSIVE
===================================== */
@media (max-width: 768px) {
  .future-section {
    padding: 1.5rem;
  }

  .chart-wrapper {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .future-section {
    padding: 1rem;
  }

  .chart-wrapper {
    height: 250px;
  }
}