/* If Section.css provides the main .section styling, 
   we only add what's necessary for this page. */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.edu-section {
  /* Let Section.css handle background, width, etc. if consistent. */
}

/* Intro text spacing */
.edu-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) VERTICAL LAYOUT
===================================== */
.edu-education,
.edu-calculator {
  /* We simply display each block as its own container stacked vertically. */
  background: #f9f9f9;
  padding: 1rem;
  margin-bottom: 2rem;  /* Ensure spacing between them */
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0, 128, 128, 0.1);
}

/* Subheadings in the education block */
.edu-education h3 {
  color: #008080;
  margin-top: 1rem;
}

.edu-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   3) CALCULATOR BLOCK
===================================== */
.edu-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.edu-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   4) INPUT SLIDERS & LABELS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
  text-align: left;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

/* =====================================
   5) RESULTS SECTION
===================================== */
.calc-results {
  margin-top: 1rem;
}

.calc-results h4 {
  margin-top: 1rem;
  color: #333;
}

.calc-results ul {
  list-style-type: none;
  padding: 0;
  margin: 1rem 0;
}

.calc-results li {
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #333;
}

.calc-results p {
  margin: 0.75rem 0;
  font-size: 0.95rem;
  color: #444;
}

/* Chart container */
.edu-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* =====================================
   6) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .edu-section {
    padding: 1.5rem; /* if Section.css doesn't handle it */
  }
}

@media (max-width: 480px) {
  .edu-section {
    padding: 1rem;
  }
}