/* If Section.css provides .section styling, we only override or customize here. */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.invest-section {
  /* Use Section.css for main layout. Override if needed. */
}

.invest-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) MAIN CONTENT LAYOUT
===================================== */
.invest-content {
  display: block; /* stack vertically */
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.invest-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem; /* spacing between blocks */
}

.invest-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.invest-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.invest-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.invest-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.invest-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   5) SLIDER & RESULTS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type='range'] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.calc-results {
  margin-top: 1rem;
}

/* Chart container */
.invest-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* =====================================
   6) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .invest-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .invest-section {
    padding: 1rem;
  }
}