/* If Section.css handles overall .section styling, we only override or customize here. */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.living-section {
  /* Leverage Section.css for spacing; override if needed */
}

/* Intro text spacing */
.living-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) VERTICAL LAYOUT
===================================== */
.living-content {
  display: block; /* stack vertically */
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.living-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem;
}

.living-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.living-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.living-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.living-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.living-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   5) INPUT SLIDERS & RESULTS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.calc-results {
  margin-top: 1rem;
}

.calc-results h4 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.calc-results p {
  margin: 0.5rem 0;
  font-size: 0.95rem;
  color: #444;
}

/* =====================================
   6) DOUGHNUT CHART CONTAINER
===================================== */
.living-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* =====================================
   7) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .living-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .living-section {
    padding: 1rem;
  }
}