/* If Section.css provides base .section styling, we only add or override. */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.retirement-section {
  /* rely on Section.css for layout/padding unless you need special tweaks */
}

.retirement-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) LAYOUT
===================================== */
.retirement-content {
  display: block;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.retirement-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem; /* spacing */
}

.retirement-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.retirement-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.retirement-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.retirement-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.retirement-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   5) SLIDERS & RESULTS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.calc-results {
  margin-top: 1rem;
}

/* Chart container */
.retirement-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* =====================================
   6) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .retirement-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .retirement-section {
    padding: 1rem;
  }
}