/* We assume you have global styles from Section.css 
   for the .section class (width, spacing, etc.) */

/* =====================================
   1) SECTION OVERRIDES (if any)
===================================== */
.cars-section {
  /* If Section.css sets background, color, etc., 
     we can override or keep them consistent. */
}

/* Intro text spacing */
.cars-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) MAIN CONTENT LAYOUT
===================================== */
.cars-content {
  /* We simply stack them vertically. */
  display: block;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left; /* matches Tools page approach */
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.cars-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem; /* Space between sections */
}

/* Force bullet text color */
.cars-education ul {
  list-style-type: disc;
  margin-left: 1.5rem;
  margin-bottom: 1rem;
  color: #333; /* ensures bullets & text are visible */
}

.cars-education li {
  margin-bottom: 0.5rem;
  font-weight: 500;
}

/* Subheadings */
.cars-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.cars-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Title & description */
.cars-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.cars-calculator p {
  margin-bottom: 1rem;
  color: #333; /* ensure text is visible */
}

/* =====================================
   5) INPUT GROUPS & RESULTS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.calc-results {
  margin-top: 1rem;
}

.calc-results h4 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.calc-results ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-bottom: 1rem;
}

.calc-results li {
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #333;
}

.calc-results p {
  margin: 0.75rem 0;
  font-size: 0.95rem;
  color: #444;
}

/* =====================================
   6) CHART CONTAINER
===================================== */
.cars-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* =====================================
   7) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .cars-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .cars-section {
    padding: 1rem;
  }
}