/* We'll rely on Section.css for base .section styles. Only adding advanced overrides. */

/* =====================================
   1) SECTION OVERRIDES
===================================== */
.masterplan-section {
  /* Rely on Section.css for layout/padding. */
}

.masterplan-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) CONTENT LAYOUT
===================================== */
.masterplan-content {
  display: block;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.masterplan-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem;
}

.masterplan-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.masterplan-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.masterplan-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.masterplan-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.masterplan-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   5) SLIDERS & RESULTS
===================================== */
.calc-inputs {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  min-width: 220px;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.3rem;
  color: #333;
}

.input-group input[type='range'] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.shock-toggle {
  margin-bottom: 1rem;
}

/* Chart container with advanced style */
.masterplan-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}

/* Responsive */
@media (max-width: 768px) {
  .masterplan-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .masterplan-section {
    padding: 1rem;
  }
}