/* If Section.css provides .section styling, we only do overrides/unique layout here */

/* =====================================
   1) TAX SECTION OVERRIDES
===================================== */
.tax-section {
  /* Use Section.css for defaults (e.g. padding, background).
     Add or override if needed. */
}

/* Intro spacing */
.tax-section p {
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

/* =====================================
   2) MAIN CONTENT LAYOUT
===================================== */
.tax-content {
  /* Stack the two blocks vertically by default */
  display: block;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: left;
}

/* =====================================
   3) EDUCATION BLOCK
===================================== */
.tax-education {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0,128,128,0.1);
  margin-bottom: 1.5rem; /* spacing between sections */
}

.tax-education h3 {
  color: #008080;
  margin: 0.5rem 0;
}

.tax-education p {
  margin-bottom: 1rem;
  color: #333;
  line-height: 1.6;
}

/* =====================================
   4) CALCULATOR BLOCK
===================================== */
.tax-calculator {
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #ddd;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.tax-calculator h3 {
  color: #008080;
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.tax-calculator p {
  margin-bottom: 1rem;
  color: #333;
}

/* =====================================
   5) SLIDERS & RESULTS
===================================== */
.calc-inputs {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333;
}

.input-group input[type="range"] {
  width: 100%;
  accent-color: #008080;
  cursor: pointer;
}

.calc-results {
  margin-top: 1rem;
}

.calc-results h4 {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.calc-results ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 1rem;
}

.calc-results li {
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #333;
}

.calc-results p {
  margin: 0.75rem 0;
  font-size: 0.95rem;
  color: #444;
}

/* Chart container */
.tax-chart-container {
  margin-top: 2rem;
  background: #fff;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* =====================================
   6) RESPONSIVE BREAKPOINTS
===================================== */
@media (max-width: 768px) {
  .tax-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .tax-section {
    padding: 1rem;
  }
}